* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 20px;
}
@media screen and (min-width: 580px) {
  .container {
    margin: 20px 40px;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    margin: 0 auto;
  }
}

.module {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  position: relative;
}
@media screen and (min-width: 780px) {
  .module {
    flex-direction: row;
    gap: 95px;
    margin-top: 96px;
  }
}
.module__image {
  margin-top: 80px;
}
@media screen and (min-width: 780px) {
  .module__image {
    flex-basis: 55%;
  }
}
@media screen and (min-width: 1024px) {
  .module__image {
    flex-basis: 35%;
    margin: 0;
  }
}
.module__image img {
  width: 100%;
}
@media screen and (min-width: 780px) {
  .module__content {
    margin-top: 80px;
    flex-basis: 40%;
  }
}
@media screen and (min-width: 1024px) {
  .module__content {
    margin-top: 0;
    flex-basis: 26%;
  }
}
.module__title {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 42px;
  color: #121212;
  margin-bottom: 24px;
}
@media screen and (min-width: 1024px) {
  .module__title {
    position: static;
  }
}
.module__text {
  font-weight: 300;
  line-height: 150%;
  margin-bottom: 32px;
}
.module__button {
  display: block;
  height: 48px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  border-radius: 32px;
  background: #E4C1AE;
  color: #fff;
  border: none;
  font-weight: 700;
  cursor: pointer;
}
@media screen and (min-width: 780px) {
  .module__button {
    margin: 0;
    width: 60%;
  }
}
@media screen and (min-width: 1024px) {
  .module__button {
    width: 128px;
  }
}